var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"white_background"},[_c('headers',{attrs:{"title":_vm.title}}),_vm._v(" "),_vm._m(0)],1)}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticStyle:{"width":"94%","margin":"0 auto","line-height":"30px"}},[_c('p',{staticClass:"tite"},[_vm._v("优谷活动帮平台个人信息保护政策")]),_vm._v(" "),_c('p',{staticClass:"text"},[_vm._v("\r\n生效日期：2021年3月8日"),_c('br'),_vm._v("\r\n\r\n        广州优谷信息技术有限公司（以下简称 “我们”）依据《中华人民共和国网络安全法》、《信息安全技术个人信息安全规范》（GB/T35273-2020）以及其他相关法律法规和技术规范制定了《优谷活动帮平台个人信息保护政策》（简称“本政策”）。为了更好的保护您的个人信息，建议您仔细阅读更新后的《优谷活动帮平台个人信息保护政策》，尤其是"),_c('span',{staticClass:"bold"},[_vm._v("以黑色加粗的条款。如您对本政策条款有任何异议或疑问，您可通过本《优谷活动帮平台个人信息保护政策》第八条“联系我们”中公布的联系方式与我们沟通。")]),_vm._v(" "),_c('br'),_vm._v("\r\n《优谷活动帮平台个人信息保护政策》目录"),_c('br'),_vm._v("\r\n一、引言；"),_c('br'),_vm._v("\r\n二、个人信息收集使用规则；"),_c('br'),_vm._v("\r\n三、我们如何保护您的个人信息安全；"),_c('br'),_vm._v("\r\n四、您如何行使您的个人信息权利；"),_c('br'),_vm._v("\r\n五、我们如何保护未成年人的个人信息；"),_c('br'),_vm._v("\r\n六、我们如何使用Cookie和同类技术；"),_c('br'),_vm._v("\r\n七、本个人信息保护政策如何更新；"),_c('br'),_vm._v("\r\n八、如何联系我们。"),_c('br'),_vm._v("\r\n\r\n一、"),_c('span',{staticClass:"bold"},[_vm._v("引言")]),_c('br'),_vm._v("\r\n        1.1《优谷活动帮平台个人信息保护政策》适用于优谷向您提供的所有服务，服务包括通过优谷网站、微信公众号、微信小程序、客户端、软件工具开发包向您提供的技术服务。"),_c('br'),_vm._v("\r\n        1.2"),_c('span',{staticClass:"bold"},[_vm._v("优谷活动帮平台（以下简称“平台”）服务中可能部分由我们的关联公司提供，但未设独立的个人信息保护政策，本政策同样适用于该部分服务。")]),_c('br'),_vm._v("\r\n        1.3请您在使用平台的各项服务前，仔细阅读并充分理解本政策的全部内容。"),_c('span',{staticClass:"bold"},[_vm._v("一旦您使用或继续使用平台的服务，即表示您同意我们按照本政策使用和处理您的相关信息。")]),_c('br'),_vm._v("\r\n        1.4您需理解，"),_c('span',{staticClass:"bold"},[_vm._v("只有在您确认并同意《优谷活动帮平台个人信息保护政策》，我们才会依据本政策收集、使用、处理和存储您的个人信息。")]),_vm._v("您有权拒绝同意本政策，但请您知悉，一旦您拒绝同意本政策，可能导致您不能使用平台的相关服务和功能，或者无法达到我们拟达到的服务效果。"),_c('br'),_vm._v("\r\n        1.5"),_c('span',{staticClass:"bold"},[_vm._v("个人信息：")]),_vm._v("指以电子或者其他方式记录的能够单独或者与其他信息结合识别自然人个人身份的各种信息，包括姓名、出生日期、身份证件号码、个人生物识别信息、住址、通信通讯联系方式、通信记录和内容、账号密码、财产信息、征信信息、行踪轨迹、住宿信息、健康生理信息、交易信息等。"),_c('br'),_vm._v("\r\n        1.6"),_c('span',{staticClass:"bold"},[_vm._v("个人敏感信息：")]),_vm._v("指一旦泄露、非法提供或滥用可能危害人身和财产安全，极易导致个人名誉、身心健康受到损害或歧视性待遇等的个人信息，包括身份证件号码、个人生物识别信息、银行账号、通信记录和内容、财产信息、征信信息、行踪轨迹、住宿信息、健康生理信息、交易信息、14岁以下（含）儿童的个人信息等。"),_c('br'),_vm._v("\r\n        1.7"),_c('span',{staticClass:"bold"},[_vm._v("个人信息删除：")]),_vm._v("指在实现日常业务功能所涉及的系统中去除个人信息的行为，使其保持不可被检索、访问的状态。"),_c('br'),_vm._v(" "),_c('span',{staticClass:"bold"},[_vm._v("二、个人信息收集使用规则")]),_c('br'),_vm._v("\r\n        "),_c('span',{staticClass:"bold"},[_vm._v("2.1我们如何收集和使用您的个人信息")]),_c('br'),_vm._v("\r\n        为了向您提供服务、保障服务的正常运行、改进和优化我们的服务，平台会根据权责一致原则、目的明确原则、选择同意原则、最小必要原则、确保安全原则、主体参与原则、公开透明等原则收集您的个人信息。我们收集和使用您的个人信息类型包括两种：第一种，我们产品/服务的核心业务功能所必需的信息，此类信息为产品/服务正常运行的必备信息，您须授权我们收集和使用。如您拒绝，您将无法正常使用我们的产品/服务。第二种，非核心业务功能可能需要收集的信息，您可以选择是否授权我们收集。如您拒绝提供，将导致某些功能无法实现或无法达到我们拟达到的效果，但不影响您对核心业务功能的正常使用。"),_c('br'),_vm._v("\r\n        "),_c('span',{staticClass:"bold"},[_vm._v("2.1.1【注册】")]),_c('br'),_vm._v("\r\n        "),_c('span',{staticClass:"bold"},[_vm._v("当您在创建平台的账号时，您须提供您在中华人民共和国境内手机号码，设置、确认您的登录密码。")]),_vm._v("您提交的手机号码用于您注册、登录、绑定账号、密码找回时接受验证码，并且作为您与平台指定的联系方式之一，用来接受相关业务通知（如新品上线、服务变更等），并且平台可通过此联系方式向您发送业务通知（含账单）或与您进行业务沟通。"),_c('br'),_vm._v(" "),_c('span',{staticClass:"bold"},[_vm._v("收集手机号码是为了满足相关法律法规的网络实名制要求，若您不提供这类信息，您可能无法正常使用我们的服务。")]),_c('br'),_vm._v("\r\n        "),_c('span',{staticClass:"bold"},[_vm._v("2.1.2【下单及订单管理】")]),_c('br'),_vm._v("\r\n        当您在订购平台的服务或订购第三方依托平台提供的商品及/或服务时，我们会通过系统为您生成购买该商品及/或服务的订单。"),_c('span',{staticClass:"bold"},[_vm._v("在下单过程中,您可能需要提供您的收货人姓名、收货地址、收货人联系电话，对于部分特殊类型的商品及/或服务您还需要提供该商品或服务所必需的其他信息。")]),_c('br'),_vm._v("\r\n        我们收集这些信息是为了帮助您顺利完成交易、保障您的交易安全、便于您查询订单信息、提供客服与售后服务及其他我们明确告知的目的。"),_c('br'),_vm._v("\r\n        您可以通过平台为其他人订购商品及/或服务，您需要提供该实际订购人的前述个人信息，并确保已取得其他人的同意。"),_c('br'),_vm._v("\r\n        "),_c('span',{staticClass:"bold"},[_vm._v("为便于您了解查询订单信息并对订单信息进行管理，我们会收集您在使用我们服务过程中产生的订单信息用于向您展示及便于您对订单进行管理。")]),_c('br'),_vm._v("\r\n        "),_c('span',{staticClass:"bold"},[_vm._v("2.1.3【支付】")]),_c('br'),_vm._v("\r\n        为完成订单支付，您可根据页面提示选择付款方式。为使我们及时获悉并确认您的支付进度及状态，为您提供售后与争议解决服务，"),_c('span',{staticClass:"bold"},[_vm._v("您同意我们可自您所选择的交易对象、微信支付所属公司或您选择的其他金融机构处收集与支付进度相关信息。")]),_c('br'),_vm._v("\r\n        "),_c('span',{staticClass:"bold"},[_vm._v("2.1.4【商品或服务交付】")]),_c('br'),_vm._v("\r\n        为保证您购买的商品及/或服务能够顺利、安全、准确送达或如约向您提供服务，我们会向商品及/或服务提供方选择的相应物流配送主体同步相关配送信息。"),_c('span',{staticClass:"bold"},[_vm._v("您知晓并同意相应物流配送主体不可避免地获知及使用您的配送信息，用于完成交付目的。")]),_c('br'),_vm._v("\r\n        为使我们及时获悉并确认交付进度及状态，向您提供售后与争议解决服务，"),_c('span',{staticClass:"bold"},[_vm._v("您同意我们可自物流相关服务主体处收集与交付进度相关的信息。")]),_c('br'),_vm._v("\r\n        "),_c('span',{staticClass:"bold"},[_vm._v("2.1.5【客服及争议处理】")]),_c('br'),_vm._v("\r\n        当您与我们联系或提出售中售后、争议纠纷处理申请时，为了保障您的账号及系统安全，我们需要您提供必要的个人信息（包括账号信息、订单信息）以核验您的身份。"),_c('br'),_vm._v("\r\n        为便于与您联系、尽快帮助您解决问题或记录相关问题的处理方案及结果，我们可能会保存您与我们的沟通、通信/通话记录及相关内容（包括账号信息、订单信息、您为了证明相关事实提供的其他信息，或您留下的联系方式信息），如果您针对具体订单进行咨询、投诉或提供建议的，我们会使用您的账号信息和订单信息。"),_c('br'),_vm._v("\r\n        "),_c('span',{staticClass:"bold"},[_vm._v("2.1.6【信息公开发布功能】")]),_c('br'),_vm._v("\r\n        您可通过我们为您提供的评论、消息互动、直播及其他信息发布功能公开发布信息，包括发布图文/视频/直播内容、发表评价及问答内容等（具体功能以平台提供的为准）。"),_c('br'),_vm._v("\r\n        "),_c('span',{staticClass:"bold"},[_vm._v("请注意，您公开发布的信息中可能会涉及您或他人的个人信息甚至个人敏感信息，如您在评价时选择上传包含个人信息的图片。请您更加谨慎地考虑，是否在使用我们的服务时共享甚至公开分享相关信息。若您公开发布的信息中涉及他人个人信息的，您需在发布前征得他人的同意。")]),_c('br'),_vm._v("\r\n        "),_c('span',{staticClass:"bold"},[_vm._v("2.1.7【信息采集】")]),_c('br'),_vm._v("\r\n        商品及/或服务的提供方为提供更优质的服务，可能使用信息采集功能。当您通过信息采集功能提交个人信息"),_c('span',{staticClass:"bold"},[_vm._v("（如姓名、手机号码、身份证号码、住址）")]),_vm._v("时，您的个人信息将被同步给商品及/或服务的提供方。"),_c('br'),_vm._v("\r\n        "),_c('span',{staticClass:"bold"},[_vm._v("2.1.8【其他附加服务】")]),_c('br'),_vm._v("\r\n        "),_c('span',{staticClass:"bold"},[_vm._v("您理解并同意，我们提供的产品/服务可能需要您在您的设备中开启您的位置信息、相机、相册、麦克风、悬浮窗、接受/读取短信的访问权限，以实现这些权限所涉及信息的收集和使用。")]),_vm._v("我们会在您使用相关服务时弹窗提示相关用途并询问您是否要开启相应权限。您可在您的设备设置查看上述权限的状态，并可自行决定这些权限随时的开启或关闭。请您注意，您开启任一权限即代表您授权我们可以收集和使用相关个人信息来为您提供对应服务，您一旦关闭任一权限即代表您取消了授权，我们将不再基于对应权限继续收集和使用相关个人信息，也无法为您提供该权限所对应的服务。但是，您关闭权限的决定不会影响此前基于您的授权所进行的信息收集及使用。"),_c('br'),_vm._v("\r\n        "),_c('span',{staticClass:"bold"},[_vm._v("您充分知晓，以下情形中，我们收集、使用个人信息无需征得您的授权同意：")]),_c('br'),_vm._v("\r\n（1）与个人信息控制者履行法律法规规定的义务相关的；"),_c('br'),_vm._v("\r\n（2）与国家安全、国防安全直接相关的；"),_c('br'),_vm._v("\r\n（3）与公共安全、公共卫生、重大公共利益直接相关的；"),_c('br'),_vm._v("\r\n（4）与刑事侦查、起诉、审判和判决执行等直接相关的；"),_c('br'),_vm._v("\r\n（5）出于维护个人信息主体或其他个人的生命、财产等重大合法权益但又很难得到本人授权同意的；"),_c('br'),_vm._v("\r\n（6）所涉及的个人信息是个人信息主体自行向社会公众公开的；"),_c('br'),_vm._v("\r\n（7）根据个人信息主体要求签订和履行合同所必需的；"),_c('br'),_vm._v("\r\n（8）从合法公开披露的信息中收集个人信息的，如合法的新闻报道、政府信息公开等渠道；"),_c('br'),_vm._v("\r\n（9）维护所提供产品或服务的安全稳定运行所必需的，如发现、处置产品或服务的故障；"),_c('br'),_vm._v("\r\n（10）个人信息控制者为新闻单位，且其开展合法的新闻报道所必需的；"),_c('br'),_vm._v("\r\n（11）个人信息控制者为学术研究机构，出于公共利益开展统计或学术研究所必要，且其对外提供学术研究或描述的结果时，对结果中所包含的个人信息进行去标识化处理的。"),_c('br'),_vm._v("\r\n        如我们停止运营平台的产品或服务，我们将及时停止继续收集您个人信息的活动，将停止运营的通知以逐一送达或公告的形式通知您，对所持有的个人信息进行删除或匿名化处理。"),_c('br'),_vm._v("\r\n        "),_c('span',{staticClass:"bold"},[_vm._v("2.2我们如何委托处理、共享、转让、公开披露您的个人信息")]),_c('br'),_vm._v("\r\n        "),_c('span',{staticClass:"bold"},[_vm._v("2.2.1委托处理")]),_vm._v("\r\n        对我们委托处理个人信息的公司、组织和个人，我们会与其签署严格的保密协定，要求他们按照我们的要求、本个人信息保护政策以及其他任何相关的保密和安全测试来处理个人信息。"),_c('br'),_vm._v("\r\n        "),_c('span',{staticClass:"bold"},[_vm._v("2.2.2共享")]),_c('br'),_vm._v("\r\n        我们不会与公司以外的任何公司、组织和个人分享您的个人信息，除非获得您的明确同意。"),_c('br'),_vm._v("\r\n（1）在您主动选择情况下共享："),_c('br'),_vm._v("\r\n        若您主动通过平台购买商品及/或服务，我们会根据您的选择，将您的订单信息与交易有关的必要信息与相关商品及/或服务的提供者共享来实现您向其购买商品或服务的需求，并促使其可以完成后续的售后服务。"),_c('br'),_vm._v("\r\n（2）在获取明确同意的情况下共享：获得您的明确同意后，我们会与其他方共享您的用户信息；"),_c('br'),_vm._v("\r\n（3）在法定情形下的共享：我们可能会根据法律法规规定、诉讼、仲裁解决需要，或按行政、司法机关依法提出的要求，对外共享您的用户信息；"),_c('br'),_vm._v("\r\n（4）为了促成交易或协助解决争议，某些情况下只有共享您的用户信息，才能促成交易或处理您与他人的纠纷或争议；"),_c('br'),_vm._v("\r\n（5）与授权合作伙伴分享：若我们委托授权合作伙伴为您提供某些服务或代表我们履行职能，我们仅会出于合法、正当、必要、特定、明确的目的共享您的信息，授权合作伙伴只能接触到为其提供服务所需信息，且不能将此信息用于任何其他目的。"),_c('br'),_vm._v("\r\n对我们与之共享个人信息的公司、组织和个人，我们会与其签署严格的数据保护协议，要求他们按照我们的说明、本个人信息保护政策以及其他任何相关的保密和安全措施来处理个人信息。"),_c('br'),_vm._v("\r\n        "),_c('span',{staticClass:"bold"},[_vm._v("2.2.3转让")]),_c('br'),_vm._v("\r\n        我们不会将您的个人信息转让给任何公司、组织和个人，"),_c('span',{staticClass:"bold"},[_vm._v("但以下情形除外")]),_vm._v("："),_c('br'),_vm._v("\r\n（1）在获取明确同意的情况下转让：获得您的明确同意后，我们会向其他方转让您的个人信息；"),_c('br'),_vm._v("\r\n（2）根据适用的法律法规、或强制性的行政或司法要求；"),_c('br'),_vm._v("\r\n（3）在涉及合并、收购或破产清算时，如涉及个人信息转让，我们会在要求新的持有您个人信息的公司、组织继续受此个人信息保护政策的约束，否则我们将要求该公司、组织重新向您征求授权同意。"),_c('br'),_vm._v(" "),_c('span',{staticClass:"bold"},[_vm._v("2.2.4公开披露")]),_c('br'),_vm._v("\r\n我们仅会在以下情况下，公开披露您的个人信息："),_c('br'),_vm._v(" "),_c('span',{staticClass:"bold"},[_vm._v("（1）获得您明确同意后；")]),_c('br'),_vm._v(" "),_c('span',{staticClass:"bold"},[_vm._v("（2）基于法律的披露：在法律、法律程序、诉讼或政府主管部门强制性要求的情况下，我们可能会公开披露您的个人信息。")]),_c('br'),_vm._v(" "),_c('span',{staticClass:"bold"},[_vm._v("（3）因需要对违规账号、欺诈行为等进行处罚公告的。")]),_c('br'),_vm._v("\r\n特别提示："),_c('span',{staticClass:"bold"},[_vm._v("对于公开披露的您的个人信息，我们会在收到公开披露申请后第一时间且审慎审查其正当性、合理性、合法性，并在公开披露时和公开披露后采取最严格个人信息安全保护措施和手段对其进行保护。")]),_c('br'),_vm._v(" "),_c('span',{staticClass:"bold"},[_vm._v("2.2.5共享、转让、公开披露您的个人信息的的例外")]),_c('br'),_vm._v("\r\n以下情形中，共享、转让、公开披露您的个人信息无需事先征得您的授权同意："),_c('br'),_vm._v(" "),_c('span',{staticClass:"bold"},[_vm._v("\r\n（1）与国家安全、国防安全直接相关的；"),_c('br'),_vm._v("\r\n（2）与公共安全、公共卫生、重大公共利益直接相关的；"),_c('br'),_vm._v("\r\n（3）与犯罪侦查、起诉、审判和判决执行等直接相关的；"),_c('br'),_vm._v("\r\n（4）出于维护个人信息主体或其他个人的生命、财产等重大合法权益但又很难得到本人同意的；"),_c('br'),_vm._v("\r\n（5）个人信息主体自行向社会公众公开的个人信息；"),_c('br'),_vm._v("\r\n（6）从合法公开披露的信息中收集个人信息的，如合法的新闻报道、政府信息公开等渠道；"),_c('br'),_vm._v("\r\n（7）根据法律规定，若我们对个人信息采取技术措施和其他必要措施进行处理，使得数据接收方无法重新识别特定个人且不能复原，则此类处理后数据的共享、转让、公开披露无需另行向您通知并征得您的同意。"),_c('br'),_vm._v("\r\n        2.2.6我们如何存储您的个人信息"),_c('br'),_vm._v("\r\n（1）我们将按照有关法律法规规定，将在中国境内收集的个人信息存储于中国境内。"),_c('br'),_vm._v("\r\n（2）一般而言，我们仅在实现目的所必需的最短时间内存储您的个人信息。但根据法律法规规定或行政、司法机构、国家部门等有权机关的要求，我们有可能更改个人信息的存储时间；"),_c('br'),_vm._v("\r\n（3）当我们的产品或服务发生停止运营的情形时，我们将以推送通知、公告等形式通知您，并在合理的期限内删除您的个人信息或进行匿名化处理。"),_c('br'),_vm._v(" "),_c('span',{staticClass:"bold"},[_vm._v("三、我们如何保护您的个人信息安全")]),_c('br')]),_vm._v("\r\n        3.1我们使用符合业界标准的安全防护措施保护您提供的个人信息，防止数据遭到未经授权的访问、公开披露、使用、修改，防止数据发生损坏或丢失。我们将在合理的安全水平内使用各种安全保护措施以保障信息的安全。例如，我们会使用加密技术、匿名化处理等手段来保护您的个人信息。"),_c('br'),_vm._v("\r\n        3.2我们建立专门的管理制度、流程和组织确保您的信息安全。如部署访问控制机制，严格限制访问个人信息的人员范围等。"),_c('br'),_vm._v("\r\n        3.3我们会采取合理可行的措施，尽力避免收集无关的个人信息。我们只会在达成本政策所述目的所需的期限内存储您的个人信息，除非需要延长存储期或受到法律的允许。"),_c('br'),_vm._v("\r\n        3.4互联网环境并非百分之百安全，我们将尽力确保或担保您发送给我们的任何信息的安全性。如果我们的物理、技术、或管理防护设施遭到破坏，导致信息被非授权访问、公开披露、篡改、或毁坏，导致您的合法权益受损，我们将承担相应的法律责任。"),_c('br'),_vm._v("\r\n        3.5在不幸发生个人信息安全事件后，我们将按照法律法规的要求，及时向您告知：安全事件的基本情况和可能的影响、我们已采取或将要采取的处置措施、您可自主防范和降低风险的建议、对您的补救措施等。我们将及时将事件相关情况以邮件、信函、电话、推送通知等方式告知您，难以逐一告知个人信息主体时，我们会采取合理、有效的方式发布公告。"),_c('br'),_vm._v("\r\n        3.6同时，我们还将按照监管部门要求，主动上报个人信息安全事件的处置情况。"),_c('br'),_vm._v(" "),_c('span',{staticClass:"bold"},[_vm._v("四、您如何行使您的个人信息权利")]),_c('br'),_vm._v("\r\n        4.1您有权查询、更正或补充您的信息。您可以通过以下方式自行进行："),_c('br'),_vm._v(" "),_c('span',{staticClass:"bold"},[_vm._v("登录平台的账号，进入“账号主页”，点击“账号设置”后查询、更正个人资料及个人账号相关信息；如果您无法通过上述方式变更个人信息，可以随时联系我们的客服。我们将在15个工作日内回复您的请求。")]),_c('br'),_vm._v("\r\n        4.2您有权删除您的个人信息："),_c('br'),_vm._v("\r\n在以下情形中，您可以向我们提出删除个人信息的请求："),_c('br'),_vm._v("\r\n（1）如果我们处理个人信息的行为违反法律法规；"),_c('br'),_vm._v("\r\n（2）如果我们收集、使用您的个人信息，却未征得您的同意；"),_c('br'),_vm._v("\r\n（3）如果我们处理个人信息的行为违反了与您的约定；"),_c('br'),_vm._v("\r\n（4）如果您不再使用我们的产品或服务，或您注销了账号；"),_c('br'),_vm._v("\r\n（5）如果我们不再为您提供产品或服务。"),_c('br'),_vm._v("\r\n        若我们决定响应您的删除请求，我们还将同时通知从我们获得您的个人信息的实体，要求其及时删除，除非法律法规另有规定，或这些实体获得您的独立授权。"),_c('br'),_vm._v("\r\n        "),_c('span',{staticClass:"bold"},[_vm._v("当您从我们的服务中删除信息后，我们可能不会立即在备份系统中删除相应信息，但会在备份更新时进行删除。")]),_c('br'),_vm._v("\r\n        4.3您有权改变您授权同意的范围："),_c('br'),_vm._v("\r\n        每个业务功能需要一些基本的个人信息才能得以完成，对于额外收集的个人信息的收集和使用，您可以给予或收回您的授权同意。"),_c('br'),_vm._v("\r\n        如果您希望变更您的授权范围，可以随时联系我们的客服。我们将在15个工作日内回复您的请求。"),_c('br'),_vm._v("\r\n        4.4您有权通过如下方式注销账号："),_c('br'),_vm._v("\r\n        如需注销账号，您可以通过本政策第八条联系我们的客服申请注销。"),_c('span',{stat